export
function callApi (apiRoot = '/', upType = 'node') {

  var obj = {}
  // var token = cookie('abc-token') || decodeURIComponent(getQueryString('authToken'))
  let confObj = {
    baseURL: apiRoot,
    timeout: 10000,
    // headers: { 'Authorization': token }
    // headers: {'content-type': 'application/x-www-form-urlencoded'} // php接口的post上传你数组使用此类型
    // headers: {'content-type': 'application/json;charset=UTF-8' } // nodejs接口的post上传你数组使用此类型
  }
  if (upType === 'node') {
    confObj.headers = {'content-type': 'application/json;charset=UTF-8' }
  } else {
    // post上传php接口的headers头
    confObj.headers = {'content-type': 'application/x-www-form-urlencoded'}
  }

  obj = axios.create(confObj)

  obj.interceptors.request.use(config => {
    return config
  }, err => {
    return Promise.reject(err)
  })

  obj.interceptors.response.use(res => {
    return res.data
  }, err => {
    // let error
    // function netError () {
    //   if (isTeacher()) {
    //     error = new Error('Network error, please check your network settings')
    //   } else {
    //     error = new Error('网络错误，请检查网络设置')
    //   }
    // }
    //
    // if (err.message === 'Network Error' || ~(err.message || '').indexOf('timeout')) {
    //   netError()
    //   return Promise.reject(error)
    // }
    //
    // const { status, data = {} } = err.response || {}
    //
    // let error = data.message ? new Error(data.message) : err
    // if (~error.message.indexOf('timeout')) {
    //   netError()
    // }

    const error = err
    return Promise.reject(error)

  })

  return obj
}

// data =》FormData模式传参
export
const createFormData = data => {
  const formData = new FormData()
  for (let i in data) {
    formData.append(i, data[i])
  }
  return formData
}



